<template>
    <div>
        <loader :status="is_saving" />
        <b-row class="m-0">
            <b-col class="p-3 bg-light d-flex align-items-center" cols="6">
                <div>
                    <h3 class="mb-0 text-uppercase"><strong>{{barangay.name}}</strong> </h3>
                    <small class="text-muted text-uppercase">BARANGAY NAME</small>
                </div>
            </b-col>
        </b-row>
        <b-row>
            <b-col class="border" cols="6">
                <span><strong>NAME</strong></span>
            </b-col>
            <b-col class="border">
                <span><strong>TYPE</strong></span>
            </b-col>
            <b-col class="border">
                <span><strong>PRECINT NO.</strong></span>
            </b-col>
        </b-row>
        <template v-for="(item, index) in leaders">
            <NestedVoterList :key="item.id" :node="item" :numbering="index+1" />
            <div class="page-break-after"></div>
        </template>
    </div>
</template>
<script>
    import SwalMixin from '@/views/mixins/Swal.js'
    import Loader from '@/views/helpers/LoaderComponent.vue';
    import NumberFormatComponent from '@/views/helpers/NumberFormatComponent.vue';
    import { assets_path } from "@/config";
    import {bus} from '@/main.js';

    import NestedVoterList from '@/views/print/NestedVoterList.vue';
    export default {
        mixins:[SwalMixin],
        components: {Loader,NumberFormatComponent,NestedVoterList},
        props:{
            id: {
                type: Number,
                default: -1
            },
        },
        data() {
            return {
                assets_path: {},
                is_saving:false,
                barangay:{},
                leaders:[],
                est_budget:0,
                table_settings:{
                    api_url:'barangays/get-voters',
                    delete_url:'',
                    fields:[
                        {key: 'precint.name', label:'PRECINT'},
                        {key: 'name', label:'NAME'},
                        {key: 'address', label:'ADDRESS'},
                        {key: 'member_type.name', label:'TYPE'},
                        {key: 'total_child', label:'MEMBERS'}
                    ]
                },
                member_types:[]
            }
        },
        computed:{
        },
        created(){
            this.id=this.$route.params.id;
            this.assets_path = assets_path;
            this.get();
            this.getLeaders();
        },
        methods: {
            get(){
                this.is_saving=true;
                this.membersTableKey++;
                this.axios.get('barangays/get',{params:{id:this.id}}).then(response => {
                    //console.log(result)
                    this.barangay = response.data;
                    this.is_saving=false;
                }).catch(error => console.log(error));
            },
            getLeaders(){
                this.is_saving=true;
                this.membersTableKey++;
                this.axios.get('barangays/get-leaders',{params:{id:this.id}}).then(response => {
                    //console.log(result)
                    this.leaders = response.data;
                    this.is_saving=false;
                }).catch(error => console.log(error));
            },

            formatNumber(input_value,decimal=0){
                return input_value.toLocaleString('en-US',{ minimumFractionDigits: decimal,maximumFractionDigits:decimal });
            },

        }
    }
</script>
<style>
    @media print {
        /* Force a new page before an element */
        .page-break-before {
            break-before: always;
        }

        /* Force a new page after an element */
        .page-break-after {
            page-break-after: always;
        }

        /* Prevent an element from breaking across pages */
        .no-page-break {
            break-inside: avoid;
        }
    }
</style>